.PageDetail {
  background-color: #F6F7F8;
  margin: 0 -100px;
}

.header {
  height: 600px;
  padding-top: 250px;
  padding-left: 200px;
}

.header .title {
  font-size: 20px;
  font-weight: 700;
  color: white;
  margin-bottom: 0;
}

.header .camp-id {
  font-size: 60px;
  font-weight: 700;
  color: white;
  margin-bottom: 0;
}

.header .desc {
  font-size: 26px;
  font-weight: 600;
  color: white;
}

.menu {
  width: 85%;

  ul {
    display: flex;
    flex-wrap: wrap;
    padding-left: 200px;
    background-color: #1F8E2B ;
  }

  ul li {
    list-style-type: none;
    width: 25%;
    font-size: 16px;
    font-weight: 600;
    padding: 12px 0px;
    text-align: center;
    color: white;
  }

}
.active {
  background-color: #006322;
}