.TacDong {
  padding-top: 40px;
  padding-left: 100px;
  padding-right: 100px;
}

.title {
  font-size: 20px;
  font-weight: 600;
  color: #6A6A6A;
  margin-bottom: 40px;
  padding-top: 20px;
  border-top: 2px solid #35973F;
}

.left {
  margin-right: 100px;
}

.number {
  font-size: 60px;
  font-weight: 600;
  color: #35973F;
  line-height: 100%;
  margin-bottom: 40px;
}

.desc {
  font-size: 50px;
  font-weight: 600;
  color: black;
  line-height: 100%;
}

.icon {
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 30px;
  }

  li {
    list-style-type: none;
    font-size: 40px;
    color: black;
  }
}

.mo {
  opacity: 0.5;
}